var checkUTF = require("./components/checkUTF.js");
var processContent = require("./components/processContent.js");
var checkByteOrderMark = require("./components/checkByteOrderMark.js");
module.exports = function (file) {
  return new Promise(function (resolve, reject) {
    var fileInfo = {
      encoding: null,
      language: null,
      confidence: {
        encoding: null,
        language: null
      }
    };
    var data = {};

    // Check the byte order mark!
    var byteOrderMarkBuffer = new FileReader();
    byteOrderMarkBuffer.onload = function () {
      var uInt8String = new Uint8Array(byteOrderMarkBuffer.result).slice(0, 4).join(" ");
      var byteOrderMark = checkByteOrderMark(uInt8String);
      if (byteOrderMark) {
        fileInfo.encoding = byteOrderMark;
        fileInfo.confidence.encoding = 1;
        var byteOrderMarkReader = new FileReader();
        byteOrderMarkReader.onload = function () {
          data.content = byteOrderMarkReader.result;
          resolve(processContent(data, fileInfo));
        };
        byteOrderMarkReader.onerror = function (err) {
          reject(err);
        };
        byteOrderMarkReader.readAsText(file, fileInfo.encoding);
      } else {
        // Read with UTF-8 first, then with ISO-8859-1
        var utfReader = new FileReader();
        utfReader.onload = function () {
          var utfContent = utfReader.result;
          var utf8 = checkUTF(utfContent);
          if (utf8) {
            fileInfo.encoding = "UTF-8";
            fileInfo.confidence.encoding = 1;
          }
          if (utf8) {
            data.content = utfContent;
            resolve(processContent(data, fileInfo));
          } else {
            var isoReader = new FileReader();
            isoReader.onload = function () {
              data.content = isoReader.result;
              resolve(processContent(data, fileInfo));
            };
            isoReader.readAsText(file, "ISO-8859-1");
          }
        };
        utfReader.onerror = function (err) {
          reject(err);
        };
        utfReader.readAsText(file, "UTF-8");
      }
    };
    byteOrderMarkBuffer.onerror = function (err) {
      reject(err);
    };
    byteOrderMarkBuffer.readAsArrayBuffer(file);
  });
};